.offer{
	&__item{
		background: #fff;
		padding: 20px;
		border: 1px solid transparent;
		border-bottom-color: #f6f8fb;
		@include clearfix;
		@include transition();
		&:hover{
			border-color: #1dbfa0;
			position: relative;
			z-index: 1;
		}
		
		&.inner{
			border: 1px solid #d0d5da;
			@include border-radius(4px);
		}
	}
	&__col{
		&.left{
			width: 62%;
			float: left;
			@include MQ(L){
				width: 100%;
				float: none;
				padding-top: 0;
			}
			@include MQ(S){
				width: 100%;
				float: none;
			}
		}
		&.right{
			width: 38%;
			float: right;
			padding-top: 47px;
			text-align: right;
			@include MQ(L){
				width: 100%;
				float: none;
				padding-top: 0;
			}
			@include MQ(S){
				width: 100%;
				float: none;
				padding-top: 0;
			}
		}
	}
	&__pic{
		position: relative;
		width: 80px;
		height: 80px;
		line-height: 80px;
		float: left;
		background: #2dc1a1;
		margin: 0 10px 20px 0;
		text-align: center;
		@include border-radius();
		img{
			vertical-align: middle;
		}
	}
	&__picRound{
		position: relative;
		width: 80px;
		height: 80px;
		float: left;
		background: #2dc1a1;
		margin: 0 10px 0 0;
		@include border-radius(50%);
	}
	&__info{
		margin-left: 90px;
	}
	&__name{
		font-size: 1rem;
		color: #000;
		margin-bottom: 13px;
	}
	&__descr{
		font-size: 0.875rem;
		color: #808080;
		height: 57px;
		overflow: hidden;
	}
	&__price{
		display: inline-block;
		font-size: 1rem;
		padding: 1px 10px;
		color: #000;
		max-width: 50%;
		width: 150px;
		text-align: center;
		margin-bottom: 25px;
	}
	&__bottom{
		clear: both;
		@include clearfix;
	}
	&__tags{
		float: left;
	}
	&__options{
		float: right;
		overflow: hidden;
	}
	&__compare{
		&Icon{
			display: none;
			vertical-align: middle;
			height: 16px;
			width: 16px;
			border: 1px solid #1dbfa0;
			color: #1dbfa0;
			font-size: 6px;
			line-height: 13px;
			@include border-radius(50%);
		}
		&Text{
			margin-left: 2px;
			//display: inline-block;
			vertical-align: middle;
		}
	}
}
.offer__instructions{
	background: #fff;
	padding: 20px;
	border: 1px solid transparent;
	border-bottom-color: #f6f8fb;
	@include clearfix;
	&Text{
		font-size: 14px;
		color: #808080;
	}
}
.compare{
	&__wrap{}
	&__row{
		position: relative;
		background: #fff;
		@include clearfix;
		&:nth-child(even){
			background: #f2f5f8;
		}
		&:hover{
			z-index: 1;
			@include box-shadow(0 0 0 1px $green);
			.compare__td{
				border-color: $green;
			}
		}
	}
	&__td{
		float: left;
		width: 26%;
		font-size: 0.875rem;
		color: #000;
		padding:11px 5px;
		text-align: center;
		border-right: 2px solid #f0f0f0;
		@include MQ(S){
			padding: 5px 1px;
			border-width: 1px;
			font-size: 0.75rem;
		}
		
		&:nth-child(1){
			width: 22%;
			padding-left: 20px;
			text-align: left;
			@include MQ(S){
				padding-left: 2px;
			}
		}
		&:nth-child(2){
			border-right-width: 1px;
		}
		&:nth-child(3){}
		&:nth-child(4){
			border: none;
		}
	}
	&__footer{
		background: #fff;
		padding: 10px 10px 10px 0;
		border-top: 2px solid #f6f8fb;
		@include clearfix;
		.compare__td{
			border: none;
			
		}
		.tumbler__text{
			float: left;
			width: 53%;
			display: block;
			white-space: normal;
			@include MQ(M){
				width: 100%;
			}
			@include MQ(S){
				font-size: 0.75rem;
			}
		}
		.tumbler__slider{
			float: left;
			margin-right: 0;
			margin-top: 6px;
			@include MQ(M){
				margin-left: 0;;
			}
		}
	}
}
.offer__inner{
	background: #fff;
}
.offer__innerHeader{
	height: 250px;
	padding: 20px;
	background: #2dc1a1;
	&Icon{
		margin-top: 73px;
		text-align: center;
	}
	&Title{
		font-size: 24px;
		color: #fff;
		margin-bottom: 5px;
	}
	.tags__item{
		
	}
}
.offer__innerBody{
	padding: 20px;
	width: 700px;
	max-width: 100%;
	margin: auto;
}
.offer__innerFooter{
	padding: 20px 20px;
}
.offer__innerForm{
	width: 340px;
	max-width: 100%;
	margin: auto;
}
.offer__innerPay{
	width: 490px;
	max-width: 100%;
	margin: auto;
	overflow: hidden;
	&Title{
		font-size: 0.875rem;
		color: #000;
		margin: 20px 0;
		text-align: center;
	}
	&Item{
		width: 33%;
		float: left;
		text-align: center;
	}
	&Pic{
		margin-bottom: 10px;
		height: 35px;
		line-height: 35px;
		img{
			display: inline-block;
			vertical-align: middle;
		}
	}
}
.offer__more{
	color: #808080;
	font-size: 0.875rem;
	position: relative;
	padding:0 30px;
	display: inline-block;
	@include transition();
	&:hover{opacity: .6;}
	&Text{}
	&Arrow{
		position: absolute;
		display: block;
		top: 50%;
		margin-top: -3px;
		right: 0;
		font-size: 6px;
		color: #808080;
	}
	
	&.active{
		.offer__moreposition{
			@include transform(rotate(180deg));
		}
	}
}