.nav-tabs{
	@include clearfix;
	&.tabs2{
		li{width: 50%;}
	}
	&.tabs3{
		li{width: 33%;}
	}
	&.tabs4{
		li{width: 25%;}
	}
	li{
		float: left;
		position: relative;
		&:before,
		&:after {
			position: absolute;
			content: '';
			height: 2px;
			width: 0;
			bottom: 0;
			background-color: #16a085;
			@include transition();
		}
		&:before{left: 50%;}
		&:after{right: 50%;}
		
		&.active{
			&:before,
			&:after{
				width: 50%;
			}
			a{
				opacity: 1!important;
				cursor: default;
				color: #000;
			}
		}
	}
	a{
		display: block;
		text-align: center;
		height: 50px;
		line-height: 50px;
		font-size: 1rem;
		color: #b4b4b4;
		@include transition();
		&:hover{opacity: .6;}
	}
}
.tab-content{}
.tab-pane{
	display: none;
	&.active{
		display: block;
	}
}