.sidebar{
	float: right;
	width: 290px;
	margin-top: $defXPad;
	&-fixed{
		position: fixed;
		top: 70px;
		right: $defXPad;
		@include MQ(M){
			position: static;
			width: 100%;
			margin-top: $defXPad_M;
		}
		@include MQ(S){
			position: static;
			margin-top: $defXPad_S;
		}
	}
	@include MQ(M){
		width: 100%;
		margin-top: $defXPad_M;
	}
	@include MQ(S){
		margin-top: $defXPad_S;
	}
	&__in{
		background: #fff;
		@include box-shadow($shadow);
		@include border-radius(4px);
	}
	&__header{
		background: #fff;
		padding: 20px;
		border-bottom: 2px solid #f6f8fb;
		color: #808080;
		font-size: 18px;
		@include border-radius(4px 4px 0 0);
		&.compare{
			max-height: 0;
			overflow: hidden;
			@include transition();
			&.active{
				max-height: 80px;
			}
		}
	}
	&__body{
		padding: 20px 20px 10px;
	}
	&__footer{
		background: #fff;
		padding: 20px;
		border-top: 2px solid #f6f8fb;
		text-align: center;
		@include border-radius(0 0 4px 4px);
	}
}
.sidebar__category{
	padding: 20px 20px 30px;
	border-bottom: 2px solid #f6f8fb;
	@include MQ(S){
		padding: 10px 10px 20px;
	}
	&Title{
		font-size: 1rem;
		color: #808080;
		margin-bottom: 10px;
	}
	&Row{
		@include clearfix;
		display: block;
		border-bottom: 1px solid #edf1f6;
		padding: 7px 0;
		margin-left: 10px;
		font-size: 0.875rem;
		color: #808080;
		@include transition();
		&:hover{opacity: .6;}
		&.mark{
			.sidebarCategory__label,
			.sidebarCategory__labeled{
				color: $green;
			}
		}
	}
	&Label{
		float: left;
		font-size: 0.875rem;
		color: #808080;
		
	}
	&Labeled{
		float: right;
		font-size: 0.875rem;
		color: $green;
	}
}
.sidebar__compare{
	&Wrap{
		max-height: 0;
		overflow: hidden;
		@include transition();
		&.active{
			max-height: 80px;
		}
		.btn{
			width: 100%;
		}
	}

}
