.row{
	position: relative;
	display: block;
	margin-bottom: 20px;
	@include transition(margin .2s ease);
	@include MQ(S){
		margin-bottom: $defXPad_S;
	}
	&-confirmEmail{
		height: 0;
		overflow: hidden;
		margin-bottom: 0;
		&.active{
			margin-bottom: 20px;
		}
	}
	&-confirmPass{
		height: 0;
		overflow: hidden;
		margin-bottom: 0;
		&.active{
			margin-bottom: 20px;
		}
	}
}
.row__line{
	border-bottom: 1px solid #edf1f6;
	padding: 5px 0;
	margin-bottom: 10px;
	@include clearfix;
	&Wrap{
		margin-bottom: 25px;
	}
	&Left{
		float: left;
		img{
			vertical-align: middle;
		}
	}
	&Right{
		float: right;
	}
	.calNum{
		font-size: 14px;
		color: #000;
	}
}
.row-just{
	display: flex;
	justify-content: space-around;
	&Item{
		display: inline-block;
	}
	&2{
		.row-justItem{
			max-width: 50%;
		}
	}
	&3{
		.row-justItem{
			max-width: 33%;
		}
	}
}
.item-just{
}

.label{
	display: block;
	font-size: 0.875rem;
	margin-bottom: 8px;
	&.type1{
		text-align: center;
	}
	&.type2{
		margin-bottom: 0;
		padding: 9px 10px 9px 0;
	}
}
label.label{
	cursor: pointer;
}

.field{
	&__wrap{
		display: block;
	}
	
	&__hint{
		position: absolute;
		top: 100%;
		right: 0;
		text-align: right;
		font-size: 12px;
		margin-top: 5px;
	}
	&__link{
		color: #808080;
		font-size: 10px;
		text-decoration: underline;
		&:hover{
			text-decoration: none;
		}
	}
}
.field-codeWrap{
	padding-top: 20px;
	text-align: center;
}
.colRight{
	text-align: right;
	&__in{
		display: inline-block;
		vertical-align: top;
		max-width: 300px;
		width: 100%;
	}
}
