.db__item{
	background: #fff;
	padding: 13px 20px 15px;
	border: 1px solid transparent;
	border-bottom-color: #f6f8fb;
	@include clearfix;
	@include transition();
	&:hover{
		border-color: $green;
		position: relative;
		z-index: 1;
	}
	@include MQ(S){
		padding: 13px 10px 5px;
	}
	
	&Pic{
		position: relative;
		width: 66px;
		height: 66px;
		float: left;
		margin: 6px 10px 20px 0;
		@include border-radius();
		@include MQ(S){
			width: 30px;
			height: 30px;
		}
	}
	&Img{
		overflow: hidden;
		width: 100%;
		@include border-radius();
	}
	&Text{
		overflow: hidden;
	}
	&Info{
		width: 36%;
		float: left;
		@include MQ(M){
			width: 50%;
		}
		@include MQ(S){
			width: 100%;
		}
	}
	&Name{
		font-size: 16px;
		color: #000;
		margin-bottom: 8px;
	}
	&Descr{
		font-size: 12px;
		color: #929292;
	}
	&Data{
		overflow: hidden;
		margin-top: 36px;
		text-align: center;
		@include MQ(M){
			clear: both;
			text-align: left;
		}
		@include MQ(S){
			clear: both;
			text-align: left;
			margin: 10px 0;
		}
		&Li{
			position: relative;
			display: inline-block;
			vertical-align: top;
			white-space: nowrap;
			text-align: center;
			padding: 0 1%;
			@include MQ(S){
				padding: 0;
			}
		}
		&Num{
			font-size: 1rem;
			color: #000;
			margin-bottom: 3px;
		}
		&Label{
			position: relative;
			padding: 0 14px;
			font-size: 0.75rem;
			color: #929292;
			@include MQ(S){
				padding: 0 10px;
			}
		}
	}
	&Footer{
		clear: both;
		overflow: hidden;
		margin-top: -9px;
		margin-right: 180px;
		@include MQ(M){
			margin-top: 10px;
			margin-right: 0;
		}
		@include MQ(S){
			margin: 10px 0;
		}
		&Li{
			float: left;
			color: #929292;
			font-size: 12px;
			margin: 0 10px 5px 0;
			padding-right: 10px;
			border-right: 1px solid #f9f9f9;
			&:last-child{
				border-right: none;
			}
			@include MQ(S){
				border: none;
			}
			.status{
				margin-right: 2px;
				vertical-align: middle;
			}
		}
	}
	
	&View{
		display: none;
		&.active{
			display: block;
		}
	}
}
.db__itemBtns {
	float: right;
	@include MQ(M) {
		
	}
	@include MQ(S) {
		float: none;
		padding: 10px 0;
		overflow: hidden;
		clear: both;
	}
}
.btn-dbOpenAcc{
	@extend .btn-transpGreen;
	float: right;
	width: 150px;
	max-width: 100%;
	margin-top: 39px;
	@include MQ(S){
		margin-top: 0;
		width: 50%;
		float: left;
	}
}
.db__itemSettings{
	display: block;
	float: right;
	clear: right;
	width: 150px;
	max-width: 100%;
	margin-top: 15px;
	font-size: 14px;
	color: #000;
	height: 20px;
	line-height: 20px;
	text-align: center;
	@include transition();
	&:hover{opacity: .6;}
	@include MQ(S){
		width: 50%;
		line-height: 34px;
		margin-top: 0;
	}
}
.db__dif{
	position: absolute;
	top: -9px;
	text-align: center;
	right: 0;
	@include MQ(S){
		right: -5px;
	}
	&.neg{
		.db__difNum{color: #ff0000;}
		.db__difIcon{
			border-top-color: #ff0000;
			border-top-width: 10px;
			border-bottom: none;
		}
	}
	&.pos{
		top: -12px;
		.db__difNum{
			color: #28bb78;
			padding-top: 1px;
		}
		.db__difIcon{
			border-bottom-color: #28bb78;
			border-bottom-width: 10px;
			border-top: none;
		}
	}
	&Num{
		font-size: 6px;
		line-height: 1.3;
	}
	&Icon{
		height: 0;
		width: 0;
		border: 6px solid transparent;
	}
}

//view-sm
.db__table{
	background: #fff;
	&Head{
		border-bottom: 1px solid #e5e5e5;
		height: 40px;
	}
	&Row{
		position: relative;
		height: 40px;
		border: 1px solid transparent;
		@include transition();
		@include clearfix;
		&:hover{
			z-index: 1;
			border-color: $green;
		}
		&:nth-child(odd){
			background: #f2f5f8;
		}
	}
}
.db__td{
	float: left;
	padding: 10px 5px;
	font-size: 0.875rem;
	color: #000;
	@include MQ(S){
		display: none;
	}
	&:nth-child(1){
		width: 18%;
		padding-left: 20px;
		@include MQ(S){
			display: block;
			width: 80%;
		}
	}
	&:nth-child(2){
		width: 8%;
		text-align: right;
	}
	&:nth-child(3){
		width: 13%;
		text-align: right;
	}
	&:nth-child(4){
		width: 10%;
		text-align: right;
	}
	&:nth-child(5){
		width: 31%;
		padding-left: 5%;
	}
	&:nth-child(6){
		width: 11%;
		text-align: center;
	}
	&:nth-child(7){
		width: 9%;
		@include MQ(S){
			display: block;
			width: 20%;
		}
	}
	&In{
		position: relative;
		max-width: 100%;
	}
	&Dif{
		position: absolute;
		top: -6px;
		right: -13px;
		font-size: 8px;
		&.neg{color: #ff0000;}
		&.pos{color: #1dbfa0;}
	}
	&Link{
		color: #16a085;
		font-size: 0.75rem;
		&:hover{
			text-decoration: underline;
		}
	}
	.status{
		margin-top: 4px;
		margin-right: 11px;
		float: left;
	}
}
.db__tableUserDD{
	display: inline-block;
	max-width: 100%;
	.ddTrigger:before{
		font-size: 5px;
		color: #c2c2c2;
	}
	.ddTrigger__text{
		display: block;
		font-size: 0.875rem;
		letter-spacing: -.2px;
		@include ellipsis;
	}
	.ddMenu{
		margin-top: 10px;
	}
}
.db__moreIcon{
	display: inline-block;
	vertical-align: middle;
	font-size: 5px;
	color: #bfbfbf;
	padding: 5px 0;
	@include transition();
	&:hover{
		cursor: pointer;
		opacity: .6;
	}
}