@import "variables";
@import "mixins";

html{height: 100%; font-size: 100%;}
body{
	height: 100%;
	min-width: 320px;
	color: #000;
	font: 400 1rem/1.25 $ff-main;
	background: $body-bg;
	overflow-x: hidden;
}
a{
	font: 400 16px $ff-main;
	text-decoration: none;
	@include transition();
}
.pr-40{
	padding-right: 40px;
}
.pr-20{
	padding-right: 20px;
}
.mt-30{
	margin-top: 30px;
}
.link{
	color: #000;
	font-size: 0.875rem;
	@include transition();
	&:hover{opacity: .6;}
}


.wrapper{
	position:relative;
	z-index: 2;
	min-height: 100%;
	height: auto !important;
	@include clearfix();

	&-with-footer{
		padding-bottom: 60px;
	}
}
.box{
	padding: 0 $defXPad;
	margin: 0 auto;
}
.logo{
	display: block;
	text-align: center;
}
.content{
	padding-left: $defXPad;
	margin: 0 $defXPad $defXPad $menuWidth;
	position: relative;
	z-index: 1;
	@include MQ(M){
		padding-left: $defXPad_M;
		margin-right: $defXPad_M;
	}
	@include MQ(S){
		padding-left: 0;
		margin: 0 $defXPad_S $defXPad_S;
	}
	&.menu-open{
		margin-left: 280px;
		@include MQ(S){
			margin-left: $defXPad_S;
		}
	}
	@include transition();
	&-align{
		@include va_middle_out;
		@include transition(none);
		height: 100vh;
		padding: 70px 0 0;
		margin: 0!important;
		&In{
			@include va_middle_in;
			padding:	10px 0;
		}
	}
	&-shop{}
	&-login{}
	&-db{}
}

.main{
	display: block;
	padding-top: $defXPad;
	@include MQ(M){
		padding-top: $defXPad_M;
	}
	@include MQ(S){
		padding-top: $defXPad_S;
	}
	.sidebar ~ &{
		margin-right: 290px;
		padding-right: $defXPad;
		//@include table_clearfix;
		
		@include MQ(M){
			padding-right: 0;
			margin-right: 0;
		}
		@include MQ(S){
			clear: both;
		}
	}
	&Section{
		background: #fff;
		margin-bottom: $defXPad;
		@include box-shadow($shadow);
		@include border-radius();
		@include MQ(M){
			margin-bottom: $defXPad_M;
		}
		@include MQ(S){
			margin-bottom: $defXPad_S;
		}
		
		&__header{
			padding: 0 20px;
			border-bottom: 1px solid #f9f9f9;
			@include MQ(S){
				padding: 0 $defXPad_S;
			}
		}
		&__title{
			font-size: 16px;
			color: #000;
			padding: 15px 0;
		}
		&__body{
			padding: 20px;
			@include MQ(S){
				padding: $defXPad_S;
			}
		}
		
	}
}
.center__wrap{
	width: 500px;
	max-width: 100%;
	margin: auto;
	background: #fff;
	font-size: 1rem;
	line-height: 1.25;
	@include box-shadow($shadow);
}

.dropArrow{
	position: relative;
	padding-right: 20px;
	&:before{
		position: absolute;
		right: 0;
		font-size: 5px;
		top: 50%;
		margin-top: -2px;
		color: #7f7f7f;
	}
}
.bar{
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	&:before,
	&:after {
		position:absolute;
		content:'';
		height: 1px;
		width:0;
		bottom: 0;
		background: #fff;
		@include transition();
	}
	&:before {left:50%;}
	&:after {right:50%;}
	
	
	&Parent{
		position: relative;
		&.active{
			.bar:before,
			.bar:after{
				width: 50%!important;
			}
		}
	}
	input:focus ~ &{
		&:before,
		&:after{
			width: 50%!important;
		}
	}
}
.tab__pane{
	display: none;
	&.active{
		display: block;
	}
}

.status{
	display: inline-block;
	width: 10px;
	height: 10px;
	@include border-radius(50%);
	&.active{
		background-color: #28bb78;
	}
	&.notActive{
		background-color: #f6a623;
	}
	
}
.textEl{
	display: block;
	max-width: 100%;
	@include ellipsis;
}
.addWidget{
	display: inline-block;
	width: 100%;
	max-width: 290px;
	//display: block;
	height: 285px;
	background: #fff;
	overflow: hidden;
	margin-bottom: $defXPad;
	@include box-shadow($shadow);
	@include transition();
	&:hover{opacity: .6;}
	@include MQ(M){
		height: 120px;
		margin-bottom: $defXPad_M;
	}
	@include MQ(S){
		margin-bottom: $defXPad_S;
	}
	&__icon{
		display: block;
		margin: 93px auto 10px;
		height: 72px;
		width: 72px;
		font-size: 36px;
		color: #c9d3d8;
		text-align: center;
		line-height: 0;
		border: 2px solid #c9d3d8;
		@include border-radius(50%);
		&:before{
			vertical-align: middle;
			line-height: 68px;
		}
		@include MQ(M){
			margin-top: 20px;
			height: 42px;
			width: 42px;
			font-size: 20px;
			&:before{
				line-height: 38px;
			}
		}
		
	}
	&__text{
		display: block;
		text-align: center;
		color: #b6b6b6;
		font-size: 0.75rem;
		line-height: 1.3;
		letter-spacing: 1px;
	}
}