.menu{
	position: fixed;
	z-index: 10;
	top: 0;
	bottom: 0;
	left: 0;
	width: $menuWidth;
	background: #343434;
	@include transition();
	@include MQ(S){
		left: -$menuWidth;
	}
	
	&.open{
		width: 280px;
		@include MQ(S){
			left: 0;
			width: 100%;
		}
		.menu__header{
			padding-left: $defXPad;
		}
		.menu__headerTitle,
		.menuUser__name,
		.nav__itemText,
		.menuUser__dd .ddTrigger:before{
			opacity: 1;
		}
		.navIcon-collapse{
			@include transform(rotate(0deg));
		}
		.nav__itemText{
			width: auto;
		}
		.menuUser__name{
			max-width: 150px;
		}
	}
	
	.scrollpane{
		height: 100%;
		width: 100%!important;
		overflow: auto;
		.jspContainer{width: 100%!important;}
		.jspPane{width: 100%!important;}
	}
	&__header{
		padding: 18px 0 15px 10px;
		border-bottom: 2px solid #3d3d3d;
		@include transition();
		&Title{
			color: #b2b2b2;
			font-size: 0.875rem;
			margin-bottom: 10px;
			white-space: nowrap;
			opacity: 0;
			@include transition();
		}
	}
	&__footer{
		position: absolute;
		left: 0;
		right: 0;
		bottom: 0;
		padding: 15px 0;
		border-top: 2px solid #3d3d3d;
		background: #343434;
		z-index: 1;
	}
	&__scroll{
		width: 100%;
		height: 100%;
		margin: -100px 0 -900px;
		padding: 100px 0 70px;
	}
	&User{
		&__dd{
			display: inline-block;
			.ddTrigger{
				&:before{
					opacity: 0;
					color: #b2b2b2;
				}
			}
			.ddMenu{
				margin-top: 10px;
			}
			&Item{
				&Icon{
					float: left;
					width: 20px;
					text-align: center;
					margin-top: 12px;
					margin-right: 10px;
				}
				&Text{
					display: block;
					overflow: hidden;
					font-size: 1rem;
					padding: 10px 0;
				}
			}
		}
		&__trigger{
			white-space: nowrap;
		}
		&__photo{
			position: relative;
			display: inline-block;
			vertical-align: middle;
			margin-right: 5px;
			width: 36px;
			height: 36px;
		}
		&__pic{
			max-width: 100%;
			max-height: 100%;
			@include border-radius(4px);
			vertical-align: bottom;
		}
		&__name{
			position: relative;
			display: inline-block;
			vertical-align: middle;
			opacity: 0;
			
			max-width: 0;
			overflow: hidden;
			font-size: .75rem;
			color: #b2b2b2;
			@include ellipsis;
			@include transition();
		}
	}
	
	.status{
		position: absolute;
		right: -4px;
		bottom: -4px;
		z-index: 1;
	}
	&__btn{
		display: none;
		width: 25px;
		float: left;
		margin: 15px 10px 15px 0;
		padding: 15px 0;
		@include transition();
		@include MQ(S){
			display: block;
		}
		&Line{
			display: block;
			background: #ccc;
			height: 2px;
			margin-bottom: 2px;
			@include transition();
			&:last-child{
				margin: 0;
			}
		}
		&.menu-open{
			padding-top: 21px;
			.menu__btnLine{
				&:nth-child(1){
					@include transform(rotate(45deg));
					margin: 0;
				}
				&:nth-child(2){
					@include transform(rotate(-45deg));
					margin: -2px 0 0;
				}
				&:last-child{
					//display: none;
					opacity: 0;
					visibility: hidden;
				}
			}
		}
	}
}
.nav{
	padding-top: 15px;
	&__item{
		display: block;
		padding: 6px 20px 9px 20px;
		color: #b2b2b2;
		white-space: nowrap;
		@include transition(all .2s ease);
		@include MQ(S){
			&:last-child{
				display: none;
			}
			&:first-child:last-child{
				display: block;
			}
		}
		&.active{
			color: #fff;
		}
		&:hover{
			color: #fff;
		}
		
		&Icon{
			width: 20px;
			display: inline-block;
			vertical-align: middle;
			text-align: center;
			margin-right: 15px;
			@include transition();
		}
		&Text{
			display: inline-block;
			vertical-align: middle;
			font-size: 1rem;
			opacity: 0;
			width: 0;
			overflow: hidden;
			padding: 10px 0;
			@include border-radius(0 5px 5px 0);
			background: #343434;
			@include transition(opacity .2s ease);
		}

	}
	&Icon-acc{font-size: 19px;}
	&Icon-user{font-size: 19px;}
	&Icon-statistic{font-size: 17px;}
	&Icon-chart{font-size: 17px;}
	&Icon-tools{font-size: 18px;}
	&Icon-key{font-size: 18px;}
	&Icon-goals{font-size: 18px;}
	&Icon-notepad{font-size: 18px;}
	&Icon-insta{font-size: 19px;}
	&Icon-instagram{font-size: 19px;}
	&Icon-shop{font-size: 18px;}
	&Icon-bag{font-size: 18px;}
	&Icon-book{font-size: 18px;}
	&Icon-management{font-size: 18px;}
	&Icon-support{font-size: 17px;}
	&Icon-msg{font-size: 17px;}
	&Icon-settings{font-size: 18px;}
	&Icon-collapse{
		font-size: 10px;
		@include transform(rotate(-180deg));
	}
	&Icon-leave{font-size: 18px;}
	
}
.menuHint{
  	cursor: pointer;
	&__wrap{
		display: none;
		position: absolute;
		z-index: 11;
		&.active{
		  display: block;
		}
  	}
	&__link{
		color: #fff;
		padding:12px 20px;
		font-size: 0.75rem;
		background: #343434;
		@include border-radius(0 5px 5px 0);
	}
}

@media only screen and (max-height: 850px) {
	.nav{
		padding: 0;
	}
	.nav__itemText{
		padding: 5px 0;
	}
}
@media only screen and (max-height: 750px) {
	.nav__itemText{
		padding: 2px 0;
	}
}
@media only screen and (max-height: 700px) {
	.nav__itemText{
		padding: 0;
	}
}
