.header{
	background: white;
	padding: 0 $defXPad;
	@include clearfix;
	@include box-shadow(0 2px 5px rgba(0, 0, 0, 0.15));
	@include MQ(S){
		padding: 0 $defXPad_S;
	}
	&-p-fixed{
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		z-index: 10;
		& ~ .content{
			margin-top: 70px;
		}
		& ~ .menu{
			top: 70px;
		}
	}
}
.logo{
	float: left;
	padding: 10px 0;
	@include MQ(S){
		padding: 24px 0;
	}
	&__pic{
		height: 50px;
		vertical-align: bottom;
		@include transition();
		&:hover{opacity: .6;}
		@include MQ(S){
			height: 20px;
		}
	}
}
.headerNotice{
	float: right;
	&__trigger{
		position: relative;
		display: block;
		margin: 19px 11px;
		padding: 5px 10px;
		@include transition();
		&:hover{opacity: .6;}
		@include MQ(S){
			margin: 19px 0;
		}
	}
	&__pic{
		display: inline-block;
		vertical-align: bottom;
		height: 20px;
	}
	&__index{
		position: absolute;
		z-index: 1;
		left: 100%;
		margin-left: -19px;
		top: 3px;
		min-width: 10px;
		height: 10px;
		padding: 0 2px;
		background-color: #d0011b;
		font-size: 8px;
		line-height: 10px;
		text-align: center;
		color: #fff;
		white-space: nowrap;
		@include border-radius(50%);
	}
}
.headerUser{
	float: right;
	margin: 17px 0;
	&__trigger{}
	&__photo{
		display: inline-block;
		vertical-align: middle;
		margin-right: 5px;
		width: 36px;
		height: 36px;
		@include MQ(S){
			margin-right: 0;
			width: 20px;
			height: 20px;
			margin: 8px 0;
		}
	}
	&__pic{
		max-width: 100%;
		max-height: 100%;
		@include border-radius(50%);
		vertical-align: bottom;
	}
	&__name{
		position: relative;
		display: inline-block;
		vertical-align: middle;
		max-width: 150px;
		font-size: 1rem;
		color: #808080;
		@include ellipsis;
		@include MQ(S){
			max-width: 100px;
		}
	}
	.ddMenu{
		margin-top: 17px;
	}
	&__ddItem{
		&Icon{
			float: left;
			width: 20px;
			text-align: center;
			margin-top: 12px;
			margin-right: 10px;
		}
		&Text{
			display: block;
			overflow: hidden;
			font-size: 1rem;
			padding: 10px 0;
		}
	}
	
}
.headerAdd{
	float: right;
	margin: 18px 0 5px;
}