.customCheck__label{
	cursor: pointer;
	display: block;
	margin: 0;
	@include transition();
	@include user-sel-n;
	&:hover{opacity: .8;}
	&Relative{
		display: inline-block;
		position: relative;
		font-size: 0;
		line-height: 0;
		vertical-align: middle;
		float: left;
	}
	&Pseudo{
		display: block;
		margin-left: 30px;
		font-size: 12px;
	}
}
.customCheck__check{
	opacity: 0;
	visibility: hidden;
	height: 16px;
	width: 16px;
	&:checked + .customCheck__checkPseudo{
		//background: #624a84;
		//border-color: #624a84;
	}
	&:checked + .customCheck__checkPseudo:before{
		font-family: icomoon, sans-serif;
		content: "\e90d";
		line-height: 1;
		color: #34c6aa;
	}
	&Pseudo{
		position: absolute;
		content: '';
		top: 0;
		left: 0;
		width: 16px;
		height: 16px;
		background: #fff;
		border: 1px solid #d0d4da;
		@include border-radius(2px);
		-webkit-transition: background .2s ease, border-color .2s ease;
		transition: background .2s ease, border-color .2s ease;
		&:before{
			position: absolute;
			content: none;
			font-size: 8px;
			top: 50%;
			left: 0;
			width: 100%;
			text-align: center;
			margin: -4px 0 0 0;
			color: #fff;
		}
	}
}


.check-map{
	overflow: hidden;
	.customCheck__labelRelative{
		margin: 4px 0;
	}
	.customCheck__labelPseudo{
		font-size: 14px;
		line-height: 25px;
		color: #000;
		@include ellipsis;
		max-width: 100%;
	}
}