.field{
	height: 34px;
	width: 100%;
	border: 1px solid #d0d5da;
	padding: 0 20px;
	background: transparent;
	color: #000;
	font-size: 14px;
	@include border-radius(4px);
	@include plhl-color(#a3a3a3);
	@include transition(all .15s ease);

	&[disabled]{
		opacity: .6!important;
	}
	&[readonly]{}
	&:focus{
		border-color: $green;
	}
	&.w100{
		width: 100px;
	}
	
	&-login{
		text-align: center;
		font-size: 13px;
	}
	&-calc{
		text-align: center;
	}
	&-code{
		width: 40px;
		padding: 0 5px;
		text-align: center;
	}
	&.noticeSearch__field{
		height: 50px;
		border: none;
		border-bottom: 1px solid #3d3d3d;
		padding: 0 110px 0 30px;
		color: #fff;
		font-size: 16px;
		&:focus{
			border-color: #3d3d3d;
		}
	}
}
.textarea{
	padding: 10px 10px;
	line-height: 1.25;
	height: 150px;
}

.btn{
	height: 34px;
	width: 100%;
	margin: auto;
	padding: 0 5px;
	text-align: center;
	background: transparent;
	font-size: 12px;
	@include user-sel-n;
	@include border-radius(4px);
	@include transition();
	
	&-transp{
		border: 1px solid transparent;
		color: grey;
		&:hover{opacity: .6;}
	}
	&-transpGreen{
		border: 1px solid #16a085;
		color: #16a085;
		&:hover{
			//opacity: .6;
			color: #fff;
			background-color: #1dbfa0;
			@include box-shadow(inset 0 1px 1px rgba(0, 0, 0, 0.25));
		}
		@at-root button#{&}{
			&:focus {
				@include box-shadow(inset 0 1px 1px rgba(0, 0, 0, 0.25));
			}
		}
	}
	&-transpGray{
		border: 1px solid #828282;
		color: #a9a9a9;
		&:hover{
			opacity: .6;
		}
		&:focus{
			@include box-shadow(inset 0 1px 1px rgba(0, 0, 0, 0.25));
		}
	}
	&-transpWhite{
		border: 1px solid #fff;
		color: #fff;
		&:hover{opacity: .6;}
	}
	&-sbm{
		@extend .btn-transpGreen;
		width: 150px;
		max-width: 100%;
	}
	&-cancel{
		@extend .btn-transpGray;
		width: 150px;
		max-width: 100%;
	}
	
	
	&-moreOffer{
		@extend .btn-transpGreen;
		float: right;
		width: 150px;
		max-width: 50%;
	}
	&-compareOffer{
		@extend .btn-transp;
		float: right;
		width: 200px;
		max-width: 50%;
		&.compared{
			.offer__compareIcon{
				display: inline-block;
			}
		}
	}
	&-openNotice{
		@extend .btn-transpWhite;
		margin-top: 20px;
	}
	&-compareMore{
		@extend .btn-transpGreen;
		margin: auto;
		width: 150px;
		max-width: 100%;
		@include MQ(S){
			font-size: 0.75rem;
		}
	}
	&-select{
		width: 180px;
		display: inline-block;
	}
}
a.btn{
	display: block;
	line-height: 34px;
}