.modal {
	display: none;
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	z-index: 1056;
	overflow: hidden;
	opacity: 0;
	@include transition();
	&.fade {
		opacity: 0;
	}
	&.in {
		opacity: 1;
	}
	&-backdrop {
		position: fixed;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		z-index: 1055;
		background-color: #000;
		@include transition();
		&.fade {
			opacity: 0;
		}
		&.in {
			opacity: 0.6;
		}
	}
	&-open{
		overflow: hidden!important;
	}
	&-dialog {
		position: absolute;
		z-index: 1050;
		margin: 0;
		width: 100%!important;
		left: 0;
		top: 0;
		bottom: 0;

		&AlignOut{
			@include va_middle_out;
			position: relative;
			height: 100%;
			text-align: center;
			overflow: auto;
		}
		&AlignIn{
			@include va_middle_in;
			font-size: 13px;
			line-height: 1.25;
			padding: 10px 0;
		}
		&Content{
			text-align: left;
			margin: auto;
			width: 500px;
			max-width: 100%;
			background: #fff;
			@include box-shadow($shadow);
			@include border-radius(4px);
			&.w-l{
				width: 940px;
			}
		}
	}
}
.modal__close{
	position: absolute;
	right: 20px;
	top: 25px;
	font-size: 20px;
	color: #bfbfbf;
	cursor: pointer;
	@include transition();
	&:hover{opacity: .6;}
}
.modal__header{
	position: relative;
	padding: 20px;
	border-bottom: 2px solid #f6f8fa;
	&Title{
		font-size: 24px;
		font-weight: 400;
		text-align: center;
		color: #000;
	}
}
.modal__body{
	background: #fff;
	padding: 0 20px 20px;
	&In{
		width: 600px;
		max-width: 100%;
		margin: auto;
		&.w-l{
			width: 700px;
		}
		&.w-xl{
			width: 100%;
		}
	}
}

//modal - addAcc
.addAcc__nav{
	&Wrap{
		border-top: 2px solid #1dbfa0;
		border-bottom: 2px solid #1dbfa0;
		margin: 0 -20px;
	}
	&List{
		position: relative;
		text-align: center;
		width: 600px;
		max-width: 100%;
		margin: auto;
		overflow: hidden;
		margin-bottom: -2px;
		@include justified;
	}
	&Li{
		position: relative;
		display: inline-block;
		vertical-align: top;
		text-align: left;
		
		&:before{
			visibility: hidden;
			opacity: 0;
			height: 0;
			position: absolute;
			content: '';
			bottom: 0;
			left: 60px;
			background: url("../images/icons/addAccArrow.png") 0 0 no-repeat;
			width: 21px;
			@include transition();
		}
		&.done,
		&.active{
			.addAcc__navItem{
				color: #000;
				cursor: default;
				opacity: 1!important;
				&Icon{
					color: #1dbfa0;
				}
			}
		}
		&.active{
			&:before{
				visibility: visible;
				opacity: 1;
				height: 13px;
			}
		}
	}
	&Item{
		display: block;
		color: #d1d1d1;
		font-size: 0.875rem;
		padding: 16px 5px 0;
		height: 80px;
		text-align-last: left;
		@include transition();
		&:hover{opacity: .6;}
		&Icon{
			display: inline-block;
			vertical-align: top;
			font-size: 46px;
			margin-right: 7px;
			@include MQ(S){
				font-size: 27px;
			}
			&.pic{
				font-size: 0;
				line-height: 0;
			}
			img{
				display: inline-block;
				vertical-align: top;
				@include MQ(S){
					height: 30px;
				}
			}
		}
		&Text{
			display: inline-block;
			vertical-align: top;
			margin-top: 4px;
		}
	}
}
.addAcc__pane{
	&Wrap{
		width: 480px;
		max-width: 100%;
		margin: auto;
	}
	display: none;
	&.active{
		display: block;
	}
	&Body{
		min-height: 415px;
		font-size: 0.875rem;
		color: #000;
		padding-top: 50px;
		@include MQ(S){
			min-height: 300px;
			padding-top: 20px;
		}
		p{
			padding-bottom: 30px;
		}
	}
	&Form{
		width: 380px;
		max-width: 100%;
		margin: auto;
		text-align: center;
		margin-bottom: 20px;
		&Item{
			padding: 0 40px;
			border: 2px solid #1dbfa0;
		}
		&Title{
			margin-top: -18px;
			text-align: center;
			margin-bottom: 10px;
			img{
				display: inline-block;
				vertical-align: top;
				padding: 0 20px;
				background: #fff;
			}
		}
		.field{
			text-align: center;
		}
	}
	&Acc{
		text-align: center;
		margin: 20px auto 0;
		width: 300px;
		max-width: 100%;
		overflow: hidden;
		&Pic{
			display: inline-block;
			img{
				display: block;
				vertical-align: top;
				width: 80px;
				height: 80px;
				border-radius: 4px;
			}
		}
		&Name{
			margin-bottom: 20px;
			font-size: 1rem;
		}
		.label{
			letter-spacing: -.2px;
		}
		.field{
			text-align: center;
		}
	}
	&Footer{
		height: 65px;
		padding-top: 15px;
		text-align: center;
		.btn{
			margin: 0 2px;
		}
	}
		
	
}

//modal - map
.map__wrap{
	@include clearfix;
	padding: 20px 0;
	background: #fff;
	margin-bottom: 20px;
	border-bottom: 1px solid #f9f9f9;
	@include MQ(S){
		border-bottom: none;
	}
}
.map__frame{
	float: left;
	height: 400px;
	width: 70%;
	@include MQ(S){
		width: 100%;
		float: none;
	}
}
.map__descr{
	float: left;
	width: 30%;
	padding-left: 20px;
	@include MQ(S){
		width: 100%;
		float: none;
		padding-left: 0;
		padding-top: 20px;
	}
}
.map__title{
	font-size: 16px;
	color: #000;
	margin-bottom: 20px;
	@include MQ(S){
		margin-bottom: $defXPad_S;
	}
}
.mapInside{
	width: 100%;
	height: 100%;
}
.map__row{
	overflow: hidden;
	padding: 10px 0;
	height: 44px;
	border-bottom: 1px solid #f6f8fb;
	@include transition();
	&.deleted{
		height: 0;
		padding: 0;
	}
}
.map__trash{
	float: right;
	padding: 5px;
	color: #d0021b;
	font-size: 15px;
	margin-left: 5px;
	@include transition();
	&:hover{opacity: .6;}
}
.map__pin{
	float: right;
	padding: 5px;
	color: #a5a5a5;
	font-size: 15px;
	@include transition();
	&:hover{opacity: .6;}
}


.map__form{
	&In{
		width: 70%;
		@include MQ(S){
			width: 100%;
		}
	}
	&Btn{
		text-align: center;
	}
}
