.breadcrumbs{
	background: #fff;
	padding: 20px 20px;
	border-bottom: 2px solid #f6f8fb;
	@include border-radius(4px 4px 0 0);
	&-page{
		padding: 10px 20px;
		border-bottom: none;
		@include MQ(S){
			padding: $defXPad_S;
		}
	}
	&__li{
		position: relative;
		display: inline-block;
		vertical-align: middle;
		margin-right: 5px;
		padding-right: 25px;
		font-size: 1rem;
		color: #808080;
		max-width: 100%;
		&.active{
			padding-right: 0;
			&:after{
				display: none;
			}
			.breadcrumbs__item{
				color: #000;
				font-size: 0.875rem;
				cursor: default;
				&:hover{opacity: 1;}
			}
		}
		&:after{
			position: absolute;
			content: "\e90a";
			font-family: icomoon, sans-serif;
			right: 0;
			top: 50%;
			margin-top: -3px;
			color: #c6c6c6;
			font-size: 6px;
			display: inline-block;
			@include transform(rotate(-90deg));
		}
		
	}
	&__item{
		display: block;
		font-size: 1rem;
		color: #808080;
		letter-spacing: -.2px;
		@include ellipsis;
		@include transition();
		&:hover{opacity: .6;}
		
		
	}
	&__nav{
		font-size: 0.875rem;
		color: #16a085;
		padding: 1px 0;
		@include transition();
		&:hover{opacity: .6;}
		&.left{
			float: left;
			.breadcrumbs__navIcon{
				padding-right: 5px;
			}
		}
		&.right{
			float: right;
			.breadcrumbs__navIcon{
				padding-left: 5px;
			}
		}
		&Icon{
			vertical-align: middle;
			font-size: 14px;
			color: #16a085;
		}
	}
}