.filter{
	&__wrap{
		position: relative;
		z-index: 2;
		padding: 0 0 30px;
		@include clearfix;
		@include MQ(S){
			padding-bottom: 15px;
		}
	}
	&__col{
		&.left{
			float: left;
		}
		&.right{
			float: right;
			text-align: right;
		}
	}
	&__item{
		
	}
	&__title{
		display: inline-block;
		color: #929292;
		font-size: 16px;
		line-height: 1.25;
	}
	&__dd{
		display: inline-block;
		.ddTrigger{
			&:before{
				color: #929292;
			}
		}
		.ddTrigger__text{
			color: #929292;
			font-size: 16px;
		}
		.ddMenu{
			min-width: 150px;
			left: auto;
			margin-top: 10px;
		}
	}
	&__type{
		display: inline-block;
		padding: 0 7px;
		@include transition();
		&:hover{opacity: .6;}
		&:last-child{
			padding-right: 0;
		}
		&Icon{
			color: #d6d6d7;
			font-size: 12px;
			line-height: 15px;
			@include transition();
		}
		&.active{
			&:hover{
				opacity: 1;
				cursor: default;
			}
			.filter__typeIcon{
				color: #b2b2b2;
				font-size: 15px;
			}
		}
	}
}