.dd{
	position: relative;
	&.open > .ddMenu{
		visibility: visible;
		opacity: 1;
	}
	&.open{
		.ddTrigger{
			&:before{
				@include transform(rotate(180deg));
			}
			&:hover{
				opacity: 1;
				cursor: default;
			}
		}
	}
	&Trigger{
		position: relative;
		display: block;
		padding-right: 15px;
		line-height: 0;
		&:hover{opacity: .6;}
		@include transition();
		&:before{
			position: absolute;
			display: block;
			right: 0;
			top: 50%;
			margin-top: -2px;
			font-size: 4px;
			color: #000;
		}
		&__text{
			font-size: 12px;
			line-height: 1.25;
			color: #000;
		}
	}
	&Menu{
		visibility: hidden;
		opacity: 0;
		position: absolute;
		z-index: 1;
		top: 100%;
		left: 0;
		right: 0;
		margin-top: -1px;
		background: #fff;
		padding: 5px;
		min-width: 150px;
		@include box-shadow($shadow);
		&:after{
			position: absolute;
			content: '';
			z-index: 1;
			background: rgba(0,0,0,.5);
			top: 100%;
			left: 0;
			right: 0;
		}
		&__list{
			overflow-x: hidden;
			overflow-y: auto;
			max-height: 400px;
		}
		&__item{}
		&__link{
			display: block;
			padding: 5px 10px;
			font-size: 13px;
			color: #000;
			@include transition();
			&:hover{
				background: rgba(0,0,0,.1);
			}
			&:active{
				background: #ece9f0;
			}
		}
	}
}

.dd-select{
	&.open{
		.ddTrigger{
			@include border-radius(4px 4px 0 0);
		}
	}
	.ddTrigger{
		height: 35px;
		padding: 10px;
		border: 1px solid #d0d5da;
		@include border-radius(4px);
		&:before{
			right: 10px;
		}
		&__text{
			font-size: 14px;
		}
	}
	.ddMenu{
		border: 1px solid #d0d5da;
	}
}