.login__header{
	overflow: hidden;
	padding: 20px;
	border: 1px solid #f0f3f7;
}
.login__title{
	cursor: pointer;
	height: 35px;
	line-height: 35px;
	@include transition();
	@include user-sel-n;
	&:hover{opacity: .6;}
	&.active{
		cursor: default;
		opacity: 1!important;
		.login__titleText{
			font-size: 24px;
			color: #000;
			padding: 0!important;
		}
		.login__titleArrow{
			font-size: 0;
		}
	}
	&.left{
		float: left;
		.login__titleText{
			padding-left: 10px;
		}
	}
	&.right{
		float: right;
		.login__titleText{
			padding-right: 10px;
		}
	}
	&Text{
		display: inline-block;
		vertical-align: top;
		font-size: 14px;
		color: #1a9172;
		@include transition(font-size .15s ease);
	}
	&Arrow{
		display: inline-block;
		vertical-align: top;
		font-size: 23px;
		line-height: 35px;
		color: #1a9172;
		@include transition(font-size .1s ease);
		backface-visibility: hidden;
	}
}
.login__body{
	padding: 25px 20px 20px;
	width: 340px;
	max-width: 100%;
	margin: auto;
	&.w-l{
		width: 600px;
	}
	.tab__pane{
		min-height: 250px;
	}
}
.login__note{
	font-size: 14px;
	margin: 0;
	text-align: center;
}
.login__footer{
	text-align: center;
	padding: 10px 20px 40px;
	
}
.forgot{
	margin-top: -15px;
	margin-bottom: -2px;
	float: right;
	color: #1a9172;
	font-size: 12px;
	@include transition();
	&:hover{
		text-decoration: underline;
	}
}
.form__footer{
	@include clearfix;
	padding-top: 30px;
	padding-bottom: 10px;
}
.remember{
	float: left;
	width: 50%;
	.customCheck__label{
		padding: 10px 0;
	}
}
.agree{
	margin: 5px 0 0;
	width: 50%;
	float: left;
	&__link{
		font-size: 12px;
		color: $green;
		&:hover{
			text-decoration: underline;
		}
	}
	.customCheck__labelRelative{
		margin-top: 4px;
	}
	.customCheck__labelPseudo{
		line-height: 9px;
	}
}