.tags{
	&-white{
		.tags__item{
			border-color: #fff;
			color: #fff;
			background: transparent;
		}
	}
	&-sidebar{
		padding: 20px;
		@include MQ(S){
			padding: 10px;
		}
	}
	&-offer{
		//margin-bottom: -10px;
	}
	&__title{
		font-size: 1rem;
		color: #808080;
		margin-bottom: 10px;
	}
	&__list{}
	&__li{
		display: inline-block;
		vertical-align: top;
		margin-right: 7px;
		margin-top: 5px;
		margin-bottom: 5px;
		&.mark{
			.tags__item{
				background: $green;
				border-color: $green;
				color: #fff;
			}
		}
	}
	&__item{
		display: block;
		padding: 5px 10px;
		height: 28px;
		background-color: #fff;
		border: 1px solid #ccc;
		border-radius: 3px;
		font-size: 0.8125rem;
		line-height: 1.25;
		color: #000;
		@include transition();
		&:hover{opacity: .6;}
	}
}
