.toast{
	&__container{
		position: fixed;
		right: 40px;
		top: 60px;
		z-index: 11;
		@include transition(right .2s ease);
		@include MQ(S){
			right: $defXPad_S;
		}
	}
	
	position: relative;
	width: 350px;
	max-width: 100%;
	background: #343434;
	padding: 0 30px 0 20px;
	overflow: hidden;
	clear: both;
	cursor: pointer;
	opacity: 0;
	max-height: 0;
	
	@include transition(all .2s ease-in-out);
	@include transform(translateY(-200%));
	&.active{
		opacity: 1;
		margin-top: 20px;
		max-height: 500px;
		@include transition(all .2s ease-in-out);
		@include transform(none);
	}
	
	&__close{
		position: absolute;
		top: 10px;
		right: 5px;
		color: #fff;
		font-size: 12px;
		cursor: pointer;
		@include transition();
		&:hover{opacity: .6;}
	}
	
}
