.tablet{
	.openKeyboard{
		overflow: hidden;
	}
}
.mobile{
	.openKeyboard{
		overflow: hidden;
	}
}


/*-------------1025 to 1280 -----------*/
@media (min-width:1025px) and (max-width: $L) {
	html{font-size: 95%;}
}


/*-------------768_to_1024-------------*/
@media (min-width: 768px) and (max-width: $M) {
	html{font-size: 90%;}
	.visible-sm{display: block;}
	.hidden-sm{display: none;}
	.col-sm{
		&-1{width: 8.2%;}
		&-2{width: 16.5%;}
		&-3{width: 25%;}
		&-4{width: 33.3%;}
		&-5{width: 41.7%;}
		&-6{width: 50%;}
		&-7{width: 58.3%;}
		&-8{width: 66.6%;}
		&-9{width: 75%;}
		&-10{width: 83.3%;}
		&-11{width: 91.6%;}
		&-12{width: 100%;}
	}
	.col-sm-12.pr-40{
		padding-right: 0;
	}
}


/*------------..._to_767--------------*/
@media (max-width: $S) {
	html{font-size: 82%;}
	.visible-xs{display: block;}
	.hidden-xs{display: none;}
	.col-xs{
		&-1{width: 8.2%;}
		&-2{width: 16.5%;}
		&-3{width: 25%;}
		&-4{width: 33.3%;}
		&-5{width: 41.7%;}
		&-6{width: 50%;}
		&-7{width: 58.3%;}
		&-8{width: 66.6%;}
		&-9{width: 75%;}
		&-10{width: 83.3%;}
		&-11{width: 91.6%;}
		&-12{width: 100%;}
	}
	.col-sm-12.pr-40{
		padding-right: 0;
	}
}


