.tools__item{
	background: #fff;
}
.tools__head{
	padding: 20px;
	border-bottom: 1px solid #f9f9f9;
	overflow: hidden;
}
.tools__body{
	&Row{
		padding: 20px;
		border-bottom: 1px solid #f9f9f9;
		overflow: hidden;
	}
}
.tools__td{
	float: left;
	padding-right: 5px;
	&-main{
		overflow: hidden;
	}
	&-options{
		float: right;
		overflow: hidden;
		text-align: right;
		white-space: nowrap;
		.btn{
			display: inline-block;
			vertical-align: top;
		}
		@include MQ(S){
			float: left;
			margin-top: 5px;
		}
	}
	&-rowName{
		overflow: hidden;
		width: 35%;
	}
	&-data{
		width: 45%;
	}
	&-control{
		float: right;
		width: 20%;
		text-align: right;
		padding-right: 0;
	}
}
.tools__pic{
	float: left;
	width: 40px;
	height: 40px;
	line-height: 40px;
	//margin-right: 20px;
	text-align: center;
	font-size: 0;
	&.bg{
		background: #2dc1a1;
	}
	img{
		vertical-align: middle;
	}
	span{
		font-size: 14px;
		vertical-align: middle;
		color: #16a085;
	}
}
.tools__title{
	font-size: 1rem;
	color: #000;
	overflow: hidden;
	padding-top: 9px;
	@include ellipsis;
	margin-left: 60px;
}
.tools__settings{
	&Text{
		display: inline-block;
		vertical-align: top;
		color: #808080;
		font-size: 0.75rem;
		padding-right: 20px;
	}
	&Icon{
		display: inline-block;
		vertical-align: top;
		font-size: 17px;
		color: #b2b2b2;
		margin-right: 10px;
		padding: 8px 10px;
		@include transition();
		&:hover{opacity: .6;}
	}
}
.tools__descr{
	color: #848484;
	font-size: 0.875rem;
	overflow: hidden;
	& > *{
		margin-bottom: 3px;
		
	}
	a{
		color: #848484;
		font-size: 0.875rem;
		@include transition();
		&:hover{opacity: .6;}
	}
	
}
.tools__data{
	&List{
		display: flex;
		justify-content: space-between;
	}
	&Item{
		display: inline-block;
		vertical-align: top;
		padding-right: 5px;
		overflow: hidden;
	}
	&Icon{
		float: left;
		padding-top: 2px;
		&.like{
			font-size: 15px;
			color: #d9534f;
		}
		&.plus{
			font-size: 11px;
			color: #28bb78;
		}
		&.minus{
			font-size: 11px;
			color: #f5a623;
		}
		&.comment{
			font-size: 13px;
			color: #1b94a3;
		}
	}
	&Info{
		color: #848484;
		overflow: hidden;
		margin-left: 30px;
		&Main{
			font-size: 0.875rem;
			margin-bottom: 2px;
			
		}
		&Add{
			font-size: 0.625rem;
		}
	}
}
.tools__more{
	float: left;
	width: 50%;
	color: #808080;
	font-size: 0.75rem;
	display: inline-block;
	padding: 8px 0;
	white-space: nowrap;
	@include transition();
	&:hover{opacity: .6;}
}
.tools__controlBtns{
	float: left;
	width: 50%;
	text-align: right;
	white-space: nowrap;
}
.tools__play,
.tools__stop{
	display: inline-block;
	padding: 5px;
	@include transition();
	&:hover{opacity: .6;}
}

.menu-open{
	@media (min-width:1281px) and (max-width: 1500px) {
		.tools__dataList{
			display: block;
		}
		.tools__dataItem{
			display: block;
			margin-bottom: 10px;
		}
		.tools__td-rowName{
			width: 45%;
		}
		.tools__td-data{
			width: 25%;
		}
		.tools__td-control{
			width: 30%;
		}
	}
}
@media (max-width: 1280px) {
	.tools__bodyRow{
		padding: 10px 20px;
	}
	.tools__td-rowName{
		float: none;
		width: 100%;
		margin-bottom: 10px;
		.tools__pic{
			text-align: left;
			width: 30px;
		}
	}
	.tools__td-data{
		float: none;
		width: 100%;
		//padding-left: 60px;
		margin-bottom: 10px;
	}
	.tools__td-control{
		float: none;
		width: 100%;
		//padding-left: 60px;
		text-align: left;
	}
	.tools__controlBtns,
	.tools__more{
		float: none;
		display: inline-block;
		width: auto;
	}
}

.console{
	background: #343434;
	@include border-radius(4px);
	color: #fff;
	font-size: 12px;
	min-height: 210px;
	.consoleScroll{
		height: 210px;
		overflow: auto;
		p{
			padding: 0 10px;
			&:last-child{
				padding-bottom: 25px;
			}
		}
	}
}
#console,#display {
  height: 210px;
}
.filterBox{
	display: none;
	&.animated{
		display: block;
	}
}