.notice__wrap{
	position: fixed;
	top: 70px;
	bottom: 0;
	width: 350px;
	right: -350px;
	z-index: 10;
	background: #343434;
	padding: 0 40px;
	@include transition(all .15s ease);
	&.active{
		right: 0;
	}
}
.noticeNav{
	&__wrap{
		position: relative;
		text-align: center;
		border-bottom: 1px solid #3d3d3d;
	}
	&__li{
		display: inline-block;
		vertical-align: top;
		&:first-child:before{
			display: block;
		}
		&:before{
			display: none;
			position: absolute;
			content: '';
			bottom: -1px;
			left: 195px;
			background: url("../images/icons/noticeArrow.png") 0 0 no-repeat;
			width: 19px;
			height: 10px;
			@include transition(left 0.2s cubic-bezier(0.55, 0.09, 0.68, 0.53));
		}
		&.active{
			&:first-child:before{
				left: 60px;
			}
			.noticeNav__item{
				color: #fff;
				cursor: default;
				opacity: 1!important;
			}
		}
	}
	&__item{
		display: block;
		color: #b2b2b2;
		font-size: 14px;
		padding: 0 20px;
		height: 40px;
		line-height: 36px;
		@include transition();
		&:hover{opacity: .6;}
	}
}
.noticePane{
	&__wrap{
		height: 100%;
		margin: 0 -40px;
		padding-top: 40px;
		margin-top: -40px;
	}
	display: none;
	height: 100%;
	&.active{
		display: block;
	}
	
	&__header{
		padding: 0 40px;
		&FieldWrap{
			display: block;
			position: relative;
		}
		
	}
	&__scroll{
		height: 100%;
		&-margin{
			margin-top: -50px;
			padding-top: 50px;
		}
	}
	.scrollpane{
		height: 100%;
		overflow: auto;
	}
	&__list{
		padding: 0 40px;
	}
	
}
.noticeSearch{
	&__wrap{
		display: block;
		position: relative;
	}
	&__icon{
		position: absolute;
		top: 50%;
		left: 0;
		color: #fff;
		font-size: 22px;
		margin-top: -11px;
	}
	&__sbm{
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		width: 22px;
		opacity: 0;
		z-index: 1;
		&:hover{
			cursor: pointer;
		}
	}
	&__sortDD{
		position: absolute;
		right: 0;
		top: 50%;
		margin-top: -5px;
		.ddTrigger{
			&:before{
				color: #fff;
			}
			&__text{
				color: #fff;
			}
		}
		.ddMenu{
			left: auto;
			width: 120px;
			margin-top: 10px;
			&__link{
				font-size: 12px;
			}
		}
		
	}
}
.noticeIcon{
	&-user{font-size: 19px;}
	&-chart{font-size: 17px;}
	&-key{font-size: 18px;}
	&-notepad{font-size: 18px;}
	&-instagram{font-size: 19px;}
	&-bag{font-size: 18px;}
	&-book{font-size: 18px;}
	&-msg{font-size: 17px;}
	&-settings{font-size: 18px;}
	
}

.noticeNote{
	&__item{
		padding: 20px 0;
		border-bottom: 1px solid #3d3d3d;
	}
	&__icon{
		float: left;
		color: #fff;
	}
	&__info{
		margin-left: 40px;
		font-size: 14px;
		color: #fff;
	}
}
.noticeMsg{
	&__li{
		border-bottom: 1px solid #3d3d3d;
	}
	&__item{
		display: block;
		padding: 20px 0;
		@include transition();
		&:hover{opacity: .6;}
	}
	&__pic{
		position: relative;
		float: left;
		width: 44px;
		height: 44px;
	}
	&__img{
		width: 100%;
		height: 100%;
		@include border-radius(50%);
		overflow: hidden;
	}
	&__userStatus{
		position: absolute;
		right: -4px;
		bottom: -4px;
		z-index: 1;
		width: 10px;
		height: 10px;
		@include border-radius(50%);
		&.red{background-color: #d9534f;}
		&.green{background-color: #28bb78;}
	}
	&__header{
		display: block;
		margin-bottom: 15px;
		margin-left: 54px;
	}
	&__title{
		display: block;
		font-size: 14px;
		padding-right: 5px;
		color: #fff;
		@include ellipsis;
	}
	&__date{
		float: right;
		font-size: 12px;
		color: #fff;
	}
	&__body{
		display: block;
		margin-left: 54px;
		font-size: 12px;
		color: #757575;
		p{
			@include ellipsis;
			max-width: 215px;
		}
	}
}