.jspContainer{
	overflow: hidden;
	position: relative;
}
.jspPane{position: absolute;}
.jspVerticalBar{
	position: absolute;
	top: 0;
	right: 0;
	width: 4px;
	height: 100%;
}
.jspHorizontalBar{
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 16px;
}
.jspVerticalBar *,
.jspHorizontalBar *{
	margin: 0;
	padding: 0;
}
.jspCap{display: none;}
.jspHorizontalBar .jspCap{float: left;}
.jspTrack{
	background: #f2f6f7;
	position: relative;
}
.jspDrag{
	background: #64d0f8;
	position: relative;
	top: 0;
	left: 0;
	cursor: pointer;
}
.jspHorizontalBar .jspTrack,
.jspHorizontalBar .jspDrag{
	float: left;
	height: 100%;
}
.jspArrow{
	background: #50506d;
	text-indent: -20000px;
	display: block;
	cursor: pointer;
}
.jspArrow.jspDisabled{
	cursor: default;
	background: #80808d;
}
.jspVerticalBar .jspArrow{
	height: 16px;
}
.jspHorizontalBar .jspArrow{
	width: 16px;
	float: left;
	height: 100%;
}
.jspVerticalBar .jspArrow:focus{outline: none;}
.jspCorner{
	background: #eeeef4;
	float: left;
	height: 100%;
}
/* Yuk! CSS Hack for IE6 3 pixel bug :( */
* html .jspCorner{margin: 0 -3px 0 0;}
//-------------------------------------------------
//.menu
.menu{
	.jspVerticalBar{
		right: 4px;
		width: 4px;
	}
	.jspTrack{
		background: transparent;
	}
	.jspDrag{
		background: rgba(255,255,255,.2);
		opacity: 1;
		&:hover {
			background: rgba(255,255,255,.2);
		}
	}
	.jspPane{width: 100%!important;}
	.jspCap{display: block;height: 5px;}
	
}



/*.dark*/
.dark{
	&:hover .jspDrag{opacity: 1;}
	.jspVerticalBar{
		right: 4px;
		width: 4px;
	}
	.jspTrack{
		background: transparent;
	}
	.jspDrag{
		background: rgba(255,255,255,.2);
		opacity: 1;
		&:hover {
			background: rgba(255,255,255,.2);
		}
	}
	.jspPane{width: 100%!important;}
	.jspCap{display: block;height: 5px;}
}

