.tumbler{
	&__label{
		position: relative;
		display: block;
		margin: 0;
		line-height: 0;
		font-size: 0;
		cursor: pointer;
		@include user-sel-n;
		&.inline{
			display: inline-block;
			vertical-align: middle;
		}
	}
	&__input{
		opacity: 0;
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		cursor: pointer;
		z-index: 1;
		height: 100%;
		padding: 0;
		&:checked {
			& ~ .tumbler__slider{
			  	background-color: $green!important;
				&:before{
					left: 22px;
				}
			}
		}
	}
	&__slider{
		display: inline-block;
		vertical-align: top;
		width: 52px;
		height: 18px;
		position: relative;
		border-radius: 3px;
		margin-right: 8px;
		&:before{
			position: absolute;
			content: "";
			background: #fff;
			width: 28px;
			height: 14px;
			top: 50%;
			left: 2px;
			margin-top: -7px;
			@include transition();
			@include border-radius(2px);
			-webkit-box-shadow: 0 3px 3px rgba(0, 0, 0, 0.05), 0 2px 2px rgba(0, 0, 0, 0.1), 0 3px 1px rgba(0, 0, 0, 0.05);
			-moz-box-shadow: 0 3px 3px rgba(0, 0, 0, 0.05), 0 2px 2px rgba(0, 0, 0, 0.1), 0 3px 1px rgba(0, 0, 0, 0.05);
			box-shadow: 0 3px 3px rgba(0, 0, 0, 0.05), 0 2px 2px rgba(0, 0, 0, 0.1), 0 3px 1px rgba(0, 0, 0, 0.05);
		}
	}
	&__text{
		overflow: hidden;
		white-space: nowrap;
		display: inline-block;
		vertical-align: top;
		font-size: 0.8125rem;
		line-height: 12px;
		& + .tumbler__slider{
			margin-left: 8px;
		}
	}
	
	&.green-white{
		.tumbler__slider{
			background: $grey;
			&:before{
				background: #fff;
			}
		}
	}
}

.tumbler-form{
	.tumbler__slider{
		margin: 8px 0;
	}
}
